body{
    margin:0;
    background-color: rgb(28, 55, 85);
    font:optional;
}


.todo-container{
    width: 500px;
    left: 50%;
    top: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    text-align: center;

}

.input-section{
    background-color:rgb(161, 161, 161);
    padding: 10px;
    border-radius: 10px;

}
.input-section input{
    border-radius: 10px;
    border: 1px solid silver;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;

}


ul {
    text-align: left;
    padding: 0;
    display: flex;
    flex-direction: column;
    
}


ul li {
    list-style: none;
    padding: 5px;
    width: 100%;
    background-color: rgb(176, 176, 168);
    height: 50px;
    border-radius: 10px;
    margin-bottom: 10px;
    box-sizing:border-box;
}

.item-del{
    float:right;
    background-color: beige;
    border-radius: 10px;
}